/*
  행사·전시
*/
@use "variables" as *;
@use "config" as *;

.exhibition-container {
  /* 공통 */
  .sub-info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    font-size: 15px;
    letter-spacing: -.7px;
    color: $gray-666;

    &-item {
      position: relative;
      padding-right: 10px;
      margin-right: 10px;

      &::after {
        content: '·';
        position: absolute;
        width: 3px;
        height: 3px;
        right: 0;
        top: 0;
        color: $gray-ccc;
      }

      &:last-child {
        padding-right: 0;
        margin-right: 0;

        &::after {
          display: none;
        }
      }
    }
  }

  .info-box {
    padding: 16px 24px;
    border-radius: 12px;
    border: solid 1px #d6e0eb;
    background-color: #eef2f7;
    margin-bottom: 40px;
    margin-top: -30px;

    .tit {
      font-size: 17px;
      font-weight: bold;
      color: #052b57;
      margin-bottom: 12px;
    }

    .desc {
      font-size: 17px;
      color: #464c53;

      + .desc {
        margin-top: 8px;
      }
    }
  }
  /* ------------------------------------ */
  .exhibition-main {
    .section-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
    }

    .section-title {
      font-size: 32px;
      font-weight: bold;
      letter-spacing: -1.5px;
    }

    .btn-more {
      display: flex;
      align-items: center;
      gap: 5px;
      justify-content: center;
      padding: 5px;
      border-radius: 6px;
      transition: .2s;
      font-size: 15px;
      letter-spacing: -0.8px;

      &:hover {
        background-color: $bg-hover;
      }

      &:active {
        background-color: $bg-active;
      }
    }

    .slider-wrapper {
      position: relative;
    }

    .btn-circle-prev, .btn-circle-next {
      position: absolute;
      width: 50px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
      border: solid 1px $gray-ccc;
      background-color: $white;
      top: 50%;
      border-radius: 100%;
      transform: translateY(-50%);
      z-index: 2;

      &.swiper-button-disabled {
        display: none;
      }
    }

    .btn-circle-prev {
      left: -60px;
    }

    .btn-circle-next {
      right: -60px;
    }

    .card-list-wrap {
      position: relative;
      padding: 0;

      .card-list {
        gap: 0;

        .card-item {
          flex: none;
        }
      }
    }

    section {
      ~ section {
        margin-top: 60px;
      }
    }

    .content-heading {
      font-size: 45px;
      font-weight: bold;
      text-align: center;
      margin-bottom: 40px;
    }

    .event-exhibit-overview {
      display: flex;
      gap: 40px;
      margin-top: 20px;
      padding: 20px 30px;
      border-radius: 10px;
      border: solid 1px #ddebf5;
      background-color: #f3f9fe;

      .calendar-area {
        flex: 1;
        min-width: 0;
      }

      .highlight-area {
        width: 400px;
        flex-shrink: 0;
        margin-top: 46px;
        background-color: $white;
        border-radius: 10px;
        box-shadow: 0 4px 10px 0 rgba(0, 54, 117, 0.1);
        overflow: hidden;

        .title {
          text-align: center;
          background-color: #003675;
          color: $white;
          height: 50px;
          line-height: 50px;
          font-size: 24px;
          font-weight: bold;
          letter-spacing: -.96px;
          padding: 0 10px;
        }

        .event-list {
          display: flex;
          flex-direction: column;
          border-top: 0;
          padding: 20px;

          .event-link {
            border-radius: 10px;
            display: flex;
            flex-direction: column;
            padding: 14px 23px;

            &:hover {
              background-color: #edf1f5;
            }

            &:active {
              background-color: $bg-active;
            }
          }

          .event-title {
            position: relative;
            font-weight: bold;
            font-size: 17px;
            letter-spacing: -.68px;
            padding-left: 30px;
            margin-bottom: 8px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;

            .ico {
              position: absolute;
              top: 2px;
              left: 0;
            }
          }

          .event-meta {
            display: flex;
            flex-direction: column;
            font-size: 13px;
            letter-spacing: -.52px;
            color: $gray-666;
            padding-left: 30px;
          }
        }
      }
    }

    .exhibition-banner {
      padding: 30px;
      border-radius: 10px;
      border: solid 1px #ddebf5;
      background-color: #f3f9fe;

      .exhibition-inner {
        display: flex;
        gap: 40px;
      }

      .exhibition-visual {
        width: 780px;
        height: 460px;
        flex-shrink: 0;
        min-width: 0;
        margin-left: 0;
        margin-right: 0;

        .exhibition-slide {
          width: 100%;
          aspect-ratio: 780 / 460;
          border-radius: 10px;
          overflow: hidden;
        }

        img {
          display: block;
          width: 100%;
          height: 100%;
        }
      }

      .exhibition-info {
        display: flex;
        flex-direction: column;
        padding-top: 30px;

        .info-title {
          font-size: 32px;
          font-weight: bold;
          letter-spacing: -1.28px;
          color: #003675;
          margin-bottom: 20px;
          word-break: keep-all;
        }

        .info-desc {
          font-size: 17px;
          letter-spacing: -0.68px;
          color: $gray-444;
          margin-bottom: 20px;
          display: -webkit-box;
          overflow: hidden;
          text-overflow: ellipsis;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 5;
        }

        .info-meta {
          display: flex;
          flex-direction: column;
          gap: 5px;
          margin-bottom: 50px;

          li {
            font-size: 17px;
            letter-spacing: -0.68px;
            color: $gray-666;

            strong {
              font-weight: bold;
              color: $gray-444;
            }
          }
        }
      }

      .btn-nav {
        position: absolute;
        bottom: 30px;
        z-index: 1;
        width: 30px;
        height: 30px;

        img {
          display: block;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        &.btn-nav-prev {
          right: 158px;
        }

        &.btn-nav-next {
          right: 120px;
        }
      }

      .page-num {
        position: absolute;
        display: inline-flex;
        align-items: center;
        width: auto;
        height: 30px;
        right: 30px;
        bottom: 30px;
        top: auto;
        left: auto;
        border-radius: 100px;
        background-color: rgba(0, 0, 0, 0.6);
        padding: 0 9px;
        z-index: 1;
        font-size: 15px;
        letter-spacing: -0.6px;

        .current {
          color: $white;
        }

        .total {
          display: flex;
          align-items: center;
          color: $gray-eee;

          &::before {
            content: '|';
            color: $gray-eee;
            margin: 1px 6px 0;
            font-size: 9px;
          }
        }
      }
    }
  }

  .exhibition-sub {
    .result-group {
      .result-group-head:only-child {
        margin-bottom: 20px;
      }
    }

    .card-list-wrap {
      padding-bottom: 60px;

      .card-list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 60px 66px;

        &.type-3 {
          grid-template-columns: repeat(3, 1fr);
          gap: 60px 74px;
        }
      }

      .no-data {
        padding: 130px 0 100px
      }
    }
  }

  .exhibition-detail {
    border-top: 1px solid $gray-666;
    
    .detail-head {
      background-color: #f9f9f9;
      border-bottom: 1px solid $gray-eee;
      padding: 15px 20px;

      .exhibition-meta {
        display: flex;
        flex-direction: column;
        gap: 5px;
      }

      .exhibition-title {
        font-size: 19px;
        letter-spacing: -0.76px;
      }

      .exhibition-summary {
        display: flex;
        flex-wrap: wrap;
        gap: 5px 0;

        .summary-item {
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          gap: 8px;

          + .summary-item {
            &::before {
              content: '|';
              margin: 0 7px 0 15px;
              color: $gray-ccc;
            }
          }

          dt {
            font-size: 17px;
            font-weight: bold;
            letter-spacing: -0.68px;
            color: $gray-444;
          }

          dd {
            font-size: 17px;
            letter-spacing: -0.68px;
            color: $gray-666;
          }
        }
      }
    }

    .detail-body {
      display: flex;
      justify-content: space-between;
      padding: 30px 20px 0;

      .detail-description {
        display: flex;
        flex-direction: column;
        gap: 30px;

        .content {
          font-size: 17px;
          line-height: 1.8;
          letter-spacing: -0.68px;
          color: $gray-444;

          .name {
            font-size: 32px;
            font-weight: bold;
            letter-spacing: -1.28px;
            margin-bottom: 20px;
          }
        }
      }

      .detail-poster {
        display: flex;
        flex-direction: column;
        gap: 20px;
        align-items: center;

        .poster-figure {
          width: 420px;
          overflow: hidden;
          border-radius: 10px;

          img {
            display: block;
            width: 100%;
            height: auto;
          }
        }
      }

      .info-list {
        .row {
          display: flex;
          border-top: 1px dashed #e3e7ee;
          padding: 7px 0;

          &:last-child {
            border-bottom: 1px dashed #e3e7ee;
          }

          dt {
            position: relative;
            width: 100px;
            padding-left: 20px;
            font-size: 15px;
            line-height: 25px;
            font-weight: 500;
            letter-spacing: -0.75px;
            color: $gray-666;

            &::before {
              content: '';
              width: 3px;
              height: 3px;
              background-color: $gray-666;
              position: absolute;
              left: 10px;
              top: 8px;
            }
          }

          dd {
            width: calc(100% - 100px);
            line-height: 25px;
            font-size: 17px;
            font-weight: 500;
            letter-spacing: -0.85px;
            color: $gray-444;
          }
        }
      }

      .actions {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 10px 16px;

        .download-list {
          .item {
            a {
              display: flex;
              align-items: center;
              gap: 10px;
              font-size: 17px;
              letter-spacing: -0.68px;
              color: $gray-444;
              border-radius: 6px;
              padding: 0 6px 0 4px;

              &:hover {
                background-color: $bg-hover;
              }

              &:active {
                background-color: $bg-active;
              }

              .ico {
                flex-shrink: 0;
              }
            }
          }
        }

        + .actions {
          margin-top: 15px;
        }

        .btn.btn-lg {
          height: auto;
          white-space: wrap;
          padding: 9.5px 15px;
          line-height: 29px;
        }
      }
    }

    .swiper-main {
      max-width: 420px;
      width: 420px;
      margin-left: 0;
      margin-right: 0;

      .poster-figure {
        width: 420px;
        overflow: hidden;
        border-radius: 10px;
        border: 1px solid $gray-ccc;

        img {
          display: block;
          width: 100%;
          height: auto;
        }
      }
    }
    
    .swiper-thumbnail-wrapper {
      position: relative;
      margin: 0 auto;
      max-width: 745px;

      .btn-nav {
        position: absolute;
        width: 24px;
        height: 24px;
        top: 50%;
        transform: translateY(-50%);
        border-right: 6px;
        transition: .2s;

        &:hover {
          background-color: $bg-hover;
        }

        &:active {
          background-color: $bg-active;
        }

        &.btn-nav-prev {
          left: -39px;
        }

        &.btn-nav-next {
          right: -39px;
        }
      }
    }

    .swiper-thumbnail {
      margin-top: 70px;
      max-width: 745px;
      padding: 6px;

      .swiper-slide {
        .img {
          aspect-ratio: 82 / 117;
          border-radius: 6px;
          border: 1px solid $gray-ccc;
          overflow: hidden;

          img {
            display: block;
            width: 100%;
            height: 100%;
            aspect-ratio: 82 / 117;
          }
        }

        .tit {
          display: block;
          font-size: 15px;
          letter-spacing: -0.6px;
          text-align: center;
          color: $gray-444;
          margin-top: 5px;
        }

        &.swiper-slide-thumb-active {
          .img {
            outline: solid 3px #256ef4;
            outline-offset: -1px;
            border: 0;
            box-shadow: 1px 2px 6px 0 rgba(36, 98, 214, 0.6);
          }
        }
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .exhibition-container {
    .info-box {
      margin-top: 0;
    }

    .exhibition-main {
      .section-header {
        position: relative;
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 27px;

        .btn-more {
          position: absolute;
          right: 0;
          top: 0;
        }

        .section-filter {
          margin-top: 23px;
        }
      }

      .section-title {
        font-size: 26px;
      }

      .content-heading {
        font-size: 32px;
      }

      .event-exhibit-overview {
        flex-direction: column;

        .highlight-area {
          padding-top: 0;
          width: 100%;
        }
      }

      .btn-circle-prev {
        left: -16px;
      }

      .btn-circle-next {
        right: -16px;
      }

      .card-list-wrap {
        padding: 0 50px;
      }

      .exhibition-banner {
        padding: 20px;

        .exhibition-inner {
          flex-direction: column;
          gap: 20px;
        }

        .exhibition-info {
          padding-top: 0;
        }

        .btn-nav {
          bottom: 20px;

          &.btn-nav-prev {
            right: 148px;
          }

          &.btn-nav-next {
            right: 110px;
          }
        }

        .page-num {
          right: 20px;
          bottom: 20px;
        }
      }
    }

    .exhibition-sub {
      .card-list-wrap {
        padding-bottom: 40px;

        .card-list {
          grid-template-columns: repeat(2, 1fr);
          gap: 40px 20px;

          .card-item {
            min-width: 0;
          }

          &.type-3 {
            grid-template-columns: repeat(2, 1fr);
            gap: 40px 20px;
          }
        }
      }
    }

    .exhibition-detail {
      .detail-head {
        .exhibition-summary {
          .summary-item {
            dt {
              font-size: 15px;
            }

            dd {
              font-size: 15px;

              &::before {
                margin: 0 5px;
              }
            }
          }
        }
      }

      .detail-body {
        flex-direction: column;
        justify-content: flex-start;
        padding: 20px 20px 0;
        gap: 30px;

        .detail-poster {
          display: flex;
          flex-direction: column;
          gap: 20px;
          align-items: center;

          .poster-figure {
            max-width: 420px;
            margin: 0 auto;
            width: 100%;
          }
        }

        .actions {
          .download-list {
            .item {
              a {
                font-size: 15px;
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .exhibition-container {
    .exhibition-detail {
      .detail-head {
        .exhibition-summary {
          gap: 5px 10px;
          .summary-item {
            + .summary-item {
              &::before {
                content: none;
              }
            }
          }
        }
      }

      .swiper-main {
        width: 100%;
      }

      .swiper-thumbnail-wrapper {
        padding: 0 40px;

        .btn-nav.btn-nav-prev {
          left: 0;
        }

        .btn-nav.btn-nav-next {
          right: 0;
        }
      }
    }
  }
}

@media (max-width: 880px) {
  .exhibition-container {
    .exhibition-main {
      .exhibition-banner {
        .exhibition-visual {
          width: 100%;
          height: auto;
          aspect-ratio: 780 / 460;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-small) {
  .exhibition-container {
    .exhibition-sub {
      .card-list-wrap {
        .card-list {
          &.type-3 {
            grid-template-columns: repeat(1, 1fr);
            gap: 30px;
          }
        }
      }
    }

    .exhibition-detail {
      .detail-head {
        padding: 15px;
      }

      .detail-body {
        padding: 20px 15px 0;
      }
    }
  }
}