/*
  보훈자료
*/
@use "variables" as *;
@use "config" as *;

.search-detail-box {
  margin-bottom: 60px;
}

.archive-search {
  &-visual {
    background-image: url(../images/archive/visual.png);
    background-size: 1280px 300px;
    background-repeat: no-repeat;
    max-width: 1280px;
    margin: 0 auto;
  }

  &-inner {
    max-width: 1280px;
    margin: 0 auto;
    padding-top: 70px;
  }

  &-title {
    font-size: 40px;
    font-weight: bold;
    letter-spacing: -2px;
    text-align: center;
    color: #3e3c62;
    margin-bottom: 6px;
  }

  &-form {
    max-width: 540px;
    margin: 0 auto 50px;

    .search-form {
      position: relative;
      display: flex;
      align-items: center;
      border-radius: 10px;
      background-color: $white;
      box-shadow: 0 3px 10px 0 rgba(36, 98, 214, 0.2);
      border: solid 3px #2462d6;

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 120px;
        width: 1px;
        height: 20px;
        background-color: #dfdfdf;
      }

      .form-select {
        flex: 0 0 120px;
        height: 60px;
        line-height: 56px;
        border: 0;
        border-radius: 0;
        background-color: transparent;
        font-size: 20px;
        padding: 0 20px;
      }

      .form-input {
        flex: 1;
        height: 60px;
        line-height: 56px;
        border: 0;
        border-radius: 0;
        background-color: transparent;
        font-size: 20px;
        padding: 0 20px;
      }

      .search-submit {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
      }
    }
  }

  &-feature {
    position: relative;
    max-width: 640px;
    margin: 0 auto;

    .slider-wrapper {
      position: relative;
      max-width: 640px;
      margin: 0 auto;
      box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2);
      border-radius: 5px;
      overflow: hidden;
    }

    .swiper-slide {
      position: relative;
      display: flex;
      align-items: center;
      background-color: #003675;
      padding: 5px;

      .link {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 1;
        top: 0;
        left: 0;
      }

      .img {
        width: 160px;
        height: 90px;
        border-radius: 2px;
        background-color: #f2e7db;
        overflow: hidden;
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
          max-width: 100%;
          max-height: 100%;
          object-fit: cover;
        }
      }

      .info {
        min-width: 0;
        flex: 1;
        word-break: keep-all;
        padding: 4px 20px 0;

        .info-title {
          font-size: 19px;
          line-height: 24px;
          font-weight: bold;
          letter-spacing: -.95px;
          color: $white;
          margin-bottom: 5px;

          &::after {
            content: '';
            display: inline-flex;
            width: 6px;
            height: 9px;
            background-image: url(../images/archive/ico-more-white.png);
            background-size: cover;
            margin-left: 7px;
            transform: translateY(-3px);
          }
        }

        .info-desc {
          font-size: 15px;
          line-height: 20px;
          letter-spacing: -0.75px;
          color: $gray-ccc;
          //display: -webkit-box;
          //overflow: hidden;
          //text-overflow: ellipsis;
          //-webkit-box-orient: vertical;
          //-webkit-line-clamp: 2;
        }
      }
    }

    .btn-nav {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 38px;
      height: 38px;
      border-radius: 100%;
      z-index: 2;

      &.btn-nav-prev {
        left: -19px;
      }

      &.btn-nav-next {
        right: -19px;
      }

      &.swiper-button-disabled {
        display: none;
      }
    }
  }
}

.archive-container {
  .result-group-wrapper {
    .result-group {
      .person-list {
        margin-bottom: 60px;
      }

      .history-list {
        margin-bottom: 30px;

        &.history-list-map {
          margin-bottom: 0;
        }
      }

      .photo-list {
        padding-bottom: 40px;
      }

      .book-list {
        padding-bottom: 40px;
      }
    }
  }

  .result-group {
    .person-list {
      grid-template-columns: repeat(6, minmax(0, 1fr));
    }

    .video-list {
      grid-template-columns: repeat(4, minmax(0, 1fr));
      gap: 30px 60px;
    }
  }

  .detail-content {
    .memorial-detail-content {
      display: flex;
      gap: 60px;
    }

    .title-area {
      margin-bottom: 20px;

      .title {
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 10px;
      }
    }
    
    .photo-slider-wrapper {
      margin-bottom: 42px;
      margin-top: 20px;

      .btn-nav {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 38px;
        height: 38px;
        z-index: 2;

        &.btn-nav-prev {
          left: 54px;
        }

        &.btn-nav-next {
          right: 54px;
        }

        &.swiper-button-disabled {
          display: none;
        }
      }

      .photo-slider {
        position: relative;
        padding: 40px 60px;
        border-radius: 20px;
        border: solid 1px #cadaee;
        background-color: #f6f8f7;

        &.only-child {
          padding: 30px 20px;
        }

        .swiper  {
          padding: 10px;
        }

        .swiper-slide {
          .img {
            display: block;
            border-radius: 10px;
            overflow: hidden;
            aspect-ratio: 220 / 165;
            transition: .2s;
            border: 2px solid transparent;

            &:hover {
              box-shadow: 0 2px 10px 0 rgba(37, 110, 244, 0.5);
              border: solid 2px #256ef4;
              background-color: #d6d6d6;
            }
          }

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }

          &:only-child {
            width: 426px !important;
            margin: 0 auto !important;

            .img {
              width: 426px;
            }
          }
        }
      }
    }

    // 기본정보
    .info-list-wrap {
      width: 370px;
      flex-shrink: 0;

      .info-list {
        .row {
          display: flex;
          border-top: 1px dashed #e3e7ee;
          padding: 7px 0;

          &:last-child {
            border-bottom: 1px dashed #e3e7ee;
          }

          dt {
            position: relative;
            width: 100px;
            padding-left: 20px;
            font-size: 15px;
            line-height: 25px;
            font-weight: 500;
            letter-spacing: -0.75px;
            color: $gray-666;

            &::before {
              content: '';
              width: 3px;
              height: 3px;
              background-color: $gray-666;
              position: absolute;
              left: 10px;
              top: 8px;
            }
          }

          dd {
            width: calc(100% - 100px);
            line-height: 25px;
            font-size: 17px;
            font-weight: 500;
            letter-spacing: -0.85px;
            color: $gray-444;
          }
        }
      }
    }

    // 건립취지
    .history-content {
      flex: 1;
      display: flex;
      flex-direction: column;
      gap: 60px;

      .h-item {
        .h-item-title {
          font-size: 24px;
          font-weight: bold;
          margin-bottom: 10px;
        }

        .h-item-desc {
          font-size: 17px;
          letter-spacing: -0.85px;
          color: $gray-444;
        }

        .h-item-mark {
          margin-top: 30px;
        }
      }
    }

    // 위치지도
    .map-area {
      width: 100%;
      border-radius: 10px;
      border: 1px solid $gray-ccc;
      overflow: hidden;

      .map {
        width: 100%;
        aspect-ratio: 880 / 360;
      }

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    // 인물 상세정보
    .featured-result {
      margin-top: 20px;
      margin-bottom: 30px;
      padding: 30px;

      .featured-result-inner {
        gap: 40px;
        justify-content: flex-start;

        .featured-result-media {
          .img {
            width: 250px;
          }
        }

        .featured-result-content {
          padding: 0;

          .featured-info {
            .title {
              font-size: 32px;
            }
          }

          .featured-meta {
            display: flex;
            flex-wrap: wrap;
            gap: 10px 40px;
            margin-bottom: 0;

            .row {
              width: calc(50% - 20px);
              gap: 20px;

              .term {
                width: 118px;
                padding-left: 10px;

                &::before {
                  left: 0;
                }
              }
            }
          }
        }
      }
    }

    .featured-info-box {
      padding: 0 27px;
      border: 1px solid #E3E7EE;
      border-radius: 10px;

      .featured-info {
        padding: 30px 0;
        display: flex;

        + .featured-info {
          border-top: 1px solid #e3e7ee;
        }

        .title {
          width: 225px;
          gap: 20px;
          flex-shrink: 0;
          font-size: 24px;
          font-weight: bold;
        }

        .info {
          flex: 1 0 0;
        }

        .meta {
          display: flex;
          flex-direction: column;
          gap: 13px;

          .row {
            display: flex;
            border-bottom: 1px dashed #e3e7ee;
            padding-bottom: 13px;

            &:last-child {
              border-bottom: 0;
            }

            .term {
              width: 100px;
              font-size: 17px;
              font-weight: 500;
              color: $gray-666;
              letter-spacing: -0.68px;
              flex-shrink: 0;
            }
            
            .desc {
              min-width: 0;
              flex: 1;
              font-size: 17px;
              letter-spacing: -0.68px;
              color: $gray-444;
            }
          }
        }
      }
    }

    .section-content {
      + .section-content {
        margin-top: 60px;
      }
    }

    // 사진 리스트
    .photo-list {
      grid-template-columns: repeat(5, minmax(0, 1fr));
      gap: 30px 44px;
      padding-bottom: 0;
    }
  }

  .relics-view-wrapper {
    padding: 40px 40px 50px;
    border: 1px solid #cadaee;
    border-radius: 20px;
    background-color: #f6f8f7;
    margin-bottom: 40px;
    margin-top: 20px;

    .img-viewer-wrapper {
      display: flex;
      justify-content: center;
      margin-bottom: 15px;
    }

    .img-viewer {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 717px;
      height: 450px;

      img {
        max-height: 100%;
        max-width: 100%;
        object-fit: contain;
      }
    }
  }
}

.popup-map-result {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 40px;

  .slider-wrapper {
    position: relative;
    width: 100%;
    overflow: hidden;
    padding: 10px 20px;
    border-radius: 20px;
    border: solid 1px #cadaee;
    background-color: #f6f8f7;

    .btn-circle-prev, .btn-circle-next {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;
      width: 38px;
      height: 38px;

      &.swiper-button-disabled {
        display: none;
      }
    }

    .btn-circle-prev {
      left: 11px;
    }

    .btn-circle-next {
      right: 11px;
    }

    .swiper {
      width: 100%;
      padding: 10px;

      .swiper-wrapper {
        display: flex;
      }

      .swiper-slide {
        width: 25%;
        flex-shrink: 0;
      }
    }

    .thumbnail {
      position: relative;
      width: 100%;
      border-radius: 10px;
      border: solid 2px transparent;
      overflow: hidden;
      transition: .2s;
      aspect-ratio: 4 / 3;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
      }

      .txt {
        position: absolute;
        width: 100%;
        text-align: center;
        padding: 5px 10px;
        font-size: 15px;
        left: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, .6);
        color: $white;
        z-index: 1;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      &:hover {
        box-shadow: 0 2px 10px 0 rgba(37, 110, 244, 0.5);
        border: solid 2px #256ef4;
        background-color: #d6d6d6;
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .search-detail-box {
    margin-bottom: 40px;
  }

  .archive-container {
    .result-group {
      .person-list {
        grid-template-columns: repeat(3, minmax(0, 1fr));
      }

      .video-list {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 30px 20px;
      }

      .photo-list {
        grid-template-columns: repeat(3, minmax(0, 1fr));
        gap: 30px 20px;
      }
    }

    .detail-content {
      .memorial-detail-content {
        gap: 20px;
      }

      // 인물 상세정보
      .featured-result {
        .featured-result-inner {
          gap: 20px;
          flex-direction: column;

          .featured-result-media {
            display: flex;
            justify-content: center;
          }
        }
      }
    }
  }

  .popup-map-result {
    .slider-wrapper {
      .swiper-slide {
        width: 25%;
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .archive-search {
    &-inner {
      padding: 70px 15px 0;
    }

    &-feature {
      padding: 0 15px;

      .btn-nav {
        &.btn-nav-prev {
          left: 0;
        }

        &.btn-nav-next {
          right: 0;
        }
      }
    }

    &-title {
      font-size: 36px;
    }

    &-form {
      .search-form {
        .form-select, .form-input {
          font-size: 17px;
          padding: 0 15px;
        }
      }
    }
  }

  .archive-container {
    .detail-content {
      .memorial-detail-content {
        flex-direction: column;
        flex-wrap: wrap;
        gap: 40px;
      }

      .photo-slider-wrapper {
        .btn-nav {
          &.btn-nav-prev {
            left: 27px;
          }

          &.btn-nav-next {
            right: 27px;
          }
        }

        .photo-slider {
          padding: 30px 30px;

          .swiper-slide {
            &:only-child {
              width: 100% !important;

              .img {
                width: 100%;
              }
            }
          }
        }
      }

      // 기본정보
      .info-list-wrap {
        width: 100%;
      }

      // 건립취지
      .history-content {
        gap: 40px;
      }

      // 인물 상세정보
      .featured-result {
        padding: 30px 20px;

        .featured-result-inner {
          .featured-result-media {
            .img {
              width: 100%;
              max-width: 250px;
            }
          }

          .featured-result-content {
            .featured-meta {
              flex-direction: column;
              gap: 10px;

              .row {
                width: 100%;
              }
            }
          }
        }
      }

      .featured-info-box {
        padding: 0 20px;

        .featured-info {
          flex-direction: column;
          padding: 20px 0;
          gap: 20px;
        }
      }
    }

    .result-group {
      .photo-list {
        grid-template-columns: repeat(2, minmax(0, 1fr));
      }
    }
  }

  .popup-map-result {
    .slider-wrapper {
      padding: 10px;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .archive-search {
    &-feature {
      .swiper-slide {
        padding: 20px;
        gap: 10px;
        flex-direction: column;

        .img {
          max-width: 160px;
          margin: 0 auto;
          width: 100%;
          height: auto;
          aspect-ratio: 160 / 90;
        }

        .info {
          padding: 0 10px;

          .info-title {
            font-size: 18px;
          }
        }
      }
    }
  }

  .archive-container {
    .result-group {
      .person-list {
        grid-template-columns: repeat(2, minmax(0, 1fr));
      }
    }

    .detail-content {
      // 인물 상세정보
      .featured-result {
        .featured-result-inner {
          .featured-result-content {
            .featured-meta {
              .row {
                flex-direction: column;
                gap: 5px;

                .desc {
                  padding-left: 10px;
                  font-size: 16px;
                }
              }
            }
          }
        }
      }

      .featured-info-box {
        .featured-info {
          .meta {
            .row {
              flex-direction: column;
              gap: 5px;

              .desc {
                font-size: 16px;
              }
            }
          }
        }

        .dot-list {
          gap: 10px;
        }
      }
    }
  }
}

// 지도검색
.map-search-container {
  padding-bottom: 8px;

  .map-container {
    position: relative;
    width: 100%;
    height: 800px;
    margin-top: 20px;
    overflow: hidden;
    box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.3);

    .map-area {
      width: 100%;
      height: 100%;
    }

    .map-search-result {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: calc(360px + 24px);
      padding-right: 24px;
      z-index: 2;
      transition: transform .25s ease, opacity .25s ease;
      will-change: transform;

      .ico-fold {
        transition: .25s;
      }

      &.is-folded {
        transform: translateX(calc(calc(-100% + 24px)));

        .ico-fold {
          transform: rotate(180deg);
        }
      }
    }

    .map-search {
      position: relative;
      height: 100%;
      background-color: #f9f9f9;
      box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.3);

      .btn-fold {
        position: absolute;
        right: -24px;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
        height: 50px;
        box-shadow: 0.7px 0.7px 4px 0 rgba(0, 0, 0, 0.3);
        background-color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 0 3px 3px 0;
      }
    }

    .map-search-form {
      background-color: $point;
      padding: 20px;

      .search-field {
        position: relative;
        margin-bottom: 10px;

        .input-search {
          width: 100%;
          height: 50px;
          box-shadow: 0 2px 3px 0 rgba(0, 54, 117, 0.3);
          background-color: $white;
          border-radius: 4px;
          border: 0;
          padding: 0 50px 0 15px;
          font-size: 17px;
          letter-spacing: -.43px;

          &::placeholder {
            color: $gray-666;
          }
        }

        .btn-search {
          position: absolute;
          right: 0;
          top: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 50px;
          height: 50px;
        }
      }

      .search-sort {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;

        .btn-sort {
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 5px;
          padding: 0 15px;
          border-radius: 4px;
          height: 30px;
          line-height: 30px;
          font-size: 15px;
          font-weight: bold;

          &.btn-sort-green {
            background-color: #eaf6ec;
            color: #287439;
          }

          &.btn-sort-blue {
            background-color: #e7f4fe;
            color: #0c6cb4;
          }

          &.btn-sort-red {
            background-color: #fdefec;
            color: #be2f13;
          }

          .ico {
            flex-shrink: 0;
          }
        }
      }
    }

    .result-head {
      display: flex;
      justify-content: space-between;
      padding: 9.5px 20px;
      border-bottom: 1px solid $gray-eee;
      background-color: $white;

      .result-count {
        font-size: 15px;
        letter-spacing: -.38px;
        color: $gray-666;

        strong {
          font-weight: 500;
          color: $point;
        }
      }
    }

    .result-list-wrap {
      max-height: 501px;
      overflow-y: auto;
    }

    .result-list {
      position: relative;
      z-index: 1;
      padding: 5px 10px 0 0;
      background-color: #f9f9f9;

      .result-item {
        position: relative;
        border-top: 1px solid $gray-eee;
        background-color: $white;

        .btn-pin {
          width: 15px;
          height: 20px;
          display: flex;
          justify-content: center;
          align-items: center;
          position: absolute;
          right: 20px;
          top: 50%;
          transform: translateY(-50%);
        }

        &.is-active, &:hover {
          &.result-item-green {
            a {
              background-color: #f8fef9;
            }
          }

          &.result-item-blue {
            a {
              background-color: #f4faff;
            }
          }

          &.result-item-red {
            a {
              background-color: #fef7f5;
            }
          }
        }

        a {
          display: block;
          padding: 14px 40px 14px 20px;
        }

        .result-title {
          font-size: 19px;
          font-weight: 500;
          letter-spacing: -0.48px;
          margin-bottom: 5px;

          .result-marker {
            display: inline-flex;
            margin-right: 5px;
          }
        }

        .result-address {
          font-size: 15px;
          line-height: 1.33;
          color: $gray-666;
        }

        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 10px;
          height: 100%;
        }

        &.result-item-green {
          &::before {
            background-color: #eaf6ec;
          }
          
          .result-marker {
            color: #008645;
          }
        }

        &.result-item-blue {
          &::before {
            background-color: #e7f4fe;
          }

          .result-marker {
            color: #2462d6;
          }
        }

        &.result-item-red {
          &::before {
            background-color: #fdefec;
          }

          .result-marker {
            color: #dc2347;
          }
        }

        &:last-child {
          border-bottom: 1px solid $gray-eee;
        }
      }
    }
  }
}

@media (max-width: 450px) {
  .map-search-container {
    .map-container {
      .map-search-result {
        width: 100%;
      }
    }
  }
}