/*
  통합검색
*/

@use "variables" as *;

.search-contents {
  .search-global {
    max-width: 920px;
    margin: 0 auto 20px;
    border-radius: 10px;
    box-shadow: 0 3px 10px 0 rgba(36, 98, 214, 0.2);
  }

  .search-tabs {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;

    .search-tabs-swiper {
      overflow: hidden;
    }

    .search-tab-list {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    .search-tab-item {
      width: auto;
      flex-shrink: 0;
    }

    .search-tab {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      height: 34px;
      line-height: 34px;
      padding: 0 15px;
      border-radius: 17px;
      white-space: nowrap;
      border: 1px solid transparent;
      background: transparent;
      cursor: pointer;
      font-size: 17px;
      font-weight: 500;
      color: $gray-444;
      transition: .2s;

      &.on, &:hover {
        background: #256ef4;
        font-weight: bold;
        color: $white;
      }
    }
  }

  .search-body {
    .inner {
      display: flex;
    }

    .search-results {
      display: flex;
      flex-direction: column;
      //gap: 50px;
      width: calc(100% - 350px - 40px);
      flex: 1;
      flex-shrink: 0;
    }

    .search-filter {
      display: flex;
      flex-direction: column;
      gap: 50px;
      border-left: 1px solid $gray-eee;
      width: 350px;
      padding-left: 40px;
      margin-left: 40px;
      flex-shrink: 0;

      .keyword-title {
        font-size: 24px;
        font-weight: bold;
        letter-spacing: -1.2px;
        margin-bottom: 15px;
      }
    }
  }
  
  .search-highlight {
    background-color: #e1ebff;
    color: $point;
    font-weight: 500;
  }

  .no-data {
    border: 1px solid #dfdfdf;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    padding: 20px;
    min-height: 100px;
  }
}

// 인물 detail
.featured-result {
  padding: 20px 50px;
  border-radius: 20px;
  border: solid 1px #ddebf5;
  background-color: #f3f9fe;
  word-break: keep-all;
  margin-bottom: 60px;

  .featured-result-inner {
    display: flex;
    justify-content: space-between;
    gap: 20px;

    .featured-result-content {
      padding-top: 20px;
      padding-bottom: 20px;

      .featured-info {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 10px;
        margin-bottom: 8px;

        .title {
          display: inline-flex;
          font-size: 24px;
          font-weight: bold;
        }

        .sub-title {
          display: inline-flex;
          font-size: 19px;
        }
      }

      .featured-meta {
        margin-bottom: 20px;

        .row {
          display: flex;

          .term {
            position: relative;
            width: 100px;
            flex-shrink: 0;
            font-size: 17px;
            font-weight: 500;
            letter-spacing: -.8px;
            color: $gray-666;
            padding-left: 20px;

            &::before {
              content: '';
              position: absolute;
              top: 12px;
              left: 8px;
              width: 3px;
              height: 3px;
              background-color: $gray-666;
            }
          }

          .desc {
            flex: 1;
            font-size: 17px;
          }
        }
      }

      .featured-actions {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
      }
    }

    .featured-result-media {
      //max-width: 180px;
      flex-shrink: 0;

      .img {
        width: 180px;
        aspect-ratio: 3 / 4;
        border-radius: 10px;
        overflow: hidden;
        border: 1px solid $gray-ccc;

        img {
          display: block;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        &.img-vertical {
          margin: 20px 0;
          width: 240px;
          aspect-ratio: 4 / 3;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .search-contents {
    .search-tabs {
      margin-bottom: 30px;
    }

    .search-head {
      .search-global {
        max-width: calc(100% - 30px);
      }

      .search-tabs {
        padding-left: 15px;

        .swiper {
          padding-right: 15px;
        }
      }
    }

    .search-body {
      .search-results {
        width: 100%;
      }

      .search-filter {
        display: none;
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {
  // 인물 detail
  .featured-result {
    padding: 20px;
  }
}

@media (max-width: 500px) {
  .featured-result {
    .featured-result-inner {
      align-items: center;
      flex-direction: column-reverse;

      .featured-result-content {
        padding-top: 0;
        padding-bottom: 0;
      }

      .featured-result-media {
        .img {
          &.img-vertical {
            margin: 0;
          }
        }
      }
    }
  }
}

// 내가 찾은 검색어
.keyword-chip-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 5px;

  .item {
    display: inline-flex;
    align-items: center;
    padding: 0 10px;
    font-size: 17px;
    letter-spacing: -.8px;
    background-color: #f4f5f6;
    border-radius: 6px;
    height: 34px;
    line-height: 34px;
    gap: 8px;

    .del {
      width: 13px;
      height: 13px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}

// 인기검색어
.keyword-rank-list {
  display: flex;
  flex-direction: column;
  gap: 14px;

  .item {
    .link {
      display: flex;
      gap: 10px;
      font-size: 17px;
      letter-spacing: -.85px;
    }

    &.item-top {
      .num {
        color: $point;
        font-weight: bold;
      }
    }

    .num {
      flex-shrink: 0;
      width: 30px;
      display: flex;
      align-items: center;
      color: $gray-444;
      font-weight: 500;
    }
  }
}